<style scoped>
.row{width: 100%;padding:1rem 0;}
.columnLeft .row{background: #fff;}
.columnLeft .bgtop {background:#eef1f6;padding: 0.5rem 0 }
.columnLeft .column{line-height: 2rem;padding: 0.5rem 0;}
.columnLeft .column:nth-child(2),.columnLeft .column:nth-child(3){border-left: 1px solid #f3f3f3;}
</style>>
<template>
<div>
   <el-button class="mt30" type="primary" size="mini">与通信服务器连接成功</el-button>
    <div class="row">
        <div class="columnLeft col3">
            <div class="row bgtop p5">
                <span class="flexLeft pl10">直播间</span>
                <el-button type="primary" size="mini" class="mr10"  @click="detail">管理</el-button>
            </div>
            <div class="row p5">
                <div class="column col5">
                    <span>正在直播</span>
                    <p>1</p>
                </div>
                 <div class="column col5">
                    <span>全部直播间</span>
                    <p>1</p>
                </div>
            </div>
        </div>
          <div class="columnLeft flexLeft ml20">
            <div class="row bgtop p5">
                <span class="flexLeft pl10">直播商品统计</span>
                <el-button type="primary" size="mini" class="mr10" @click="detail">更多</el-button>
            </div>
            <div class="row p5">
                <div class="column col3">
                    <span>当天销售额</span>
                    <p class="red">1</p>
                </div>
                 <div class="column flex">
                    <span>七天内销售额</span>
                    <p class="red">1</p>
                </div>
                 <div class="column col3">
                    <span>三十天内销售额</span>
                    <p class="red">1</p>
                </div>
            </div>
        </div>
      
    </div>
</div>
</template>
<script>
export default {
    data() {
      return {}
    },
    methods: {
        detail(){
            this.$router.push({
                name:'directseeding'
            })
        },
    },
}
</script>>


